@-webkit-keyframes fade-and-move {
    0% {
        opacity: 0;
        -webkit-transform: translateY(5rem);
                transform: translateY(5rem);
    }

    10% {
        opacity: 0.8;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    85% {
        opacity: 0.8;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    100% {
        opacity: 0;
        -webkit-transform: translateY(-5rem);
                transform: translateY(-5rem);
        visibility: hidden;
        overflow: hidden;
    }
}

@keyframes fade-and-move {
    0% {
        opacity: 0;
        -webkit-transform: translateY(5rem);
                transform: translateY(5rem);
    }

    10% {
        opacity: 0.8;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    85% {
        opacity: 0.8;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }

    100% {
        opacity: 0;
        -webkit-transform: translateY(-5rem);
                transform: translateY(-5rem);
        visibility: hidden;
        overflow: hidden;
    }
}

