#roll:checked ~ #platform > .dice:nth-child(1) {
    -webkit-animation: spin 2s infinite linear;
            animation: spin 2s infinite linear;
}

#roll:checked ~ #platform > .dice:nth-child(2) {
    -webkit-animation: spin-duplicate 2s infinite linear;
            animation: spin-duplicate 2s infinite linear;
}

#roll:checked ~ #platform {
    width: 200px;
    height: 100px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: roll 1.6s infinite linear;
            animation: roll 1.6s infinite linear;
}

#dice-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 100%;
    -webkit-perspective: 600px;
            perspective: 600px;
}

#platform {
    height: 100px;
    -webkit-align-self: center;
            align-self: center;
    width: 200px;
    padding: 20px 0;
    margin-right: 10px;
}

.dice span {
    position: absolute;
    margin: 50px 0 0 50px;
    display: block;
    font-size: 2.5em;
    padding: 5px;
}

.dice {
    position: absolute;
    width: 100px;
    height: 100px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: spin 50s infinite linear;
            animation: spin 50s infinite linear;
}

.side {
    position: absolute;
    width: 100px;
    height: 100px;
    background: #fff;
    box-shadow: inset 0 0 20px #ccc;
    border-radius: 20px;
}

.dice .cover, .dice .inner {
    background: #e0e0e0;
    box-shadow: none;
}

.dice .cover {
    border-radius: 0;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
}

.dice .cover.x {
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
}

.dice .cover.z {
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
}

.dice .front {
    -webkit-transform: translateZ(50px);
            transform: translateZ(50px);
}

.dice .front.inner {
    -webkit-transform: translateZ(49px);
            transform: translateZ(49px);
}

.dice .back {
    -webkit-transform: rotateX(-180deg) translateZ(50px);
            transform: rotateX(-180deg) translateZ(50px);
}

.dice .back.inner {
    -webkit-transform: rotateX(-180deg) translateZ(49px);
            transform: rotateX(-180deg) translateZ(49px);
}

.dice .right {
    -webkit-transform: rotateY(90deg) translateZ(50px);
            transform: rotateY(90deg) translateZ(50px);
}

.dice .right.inner {
    -webkit-transform: rotateY(90deg) translateZ(49px);
            transform: rotateY(90deg) translateZ(49px);
}

.dice .left {
    -webkit-transform: rotateY(-90deg) translateZ(50px);
            transform: rotateY(-90deg) translateZ(50px);
}

.dice .left.inner {
    -webkit-transform: rotateY(-90deg) translateZ(49px);
            transform: rotateY(-90deg) translateZ(49px);
}

.dice .top {
    -webkit-transform: rotateX(90deg) translateZ(50px);
            transform: rotateX(90deg) translateZ(50px);
}

.dice .top.inner {
    -webkit-transform: rotateX(90deg) translateZ(49px);
            transform: rotateX(90deg) translateZ(49px);
}

.dice .bottom {
    -webkit-transform: rotateX(-90deg) translateZ(50px);
            transform: rotateX(-90deg) translateZ(50px);
}

.dice .bottom.inner {
    -webkit-transform: rotateX(-90deg) translateZ(49px);
            transform: rotateX(-90deg) translateZ(49px);
}

